<template lang="html">
  <div class="card daycard today">
    <div class="cardimg daytop">
      <div class="daytext"><strong> {{ day }}</strong><div class="weekday">{{ weekDay }}</div></div>
    </div>
    <div class="container user">
      <ul class="userlist">
        <li class="listentry">some user</li>
        <li class="listentry">some other user</li>
      </ul>
    </div>
  </div>

</template>

<script>
export default {
  props: {
    date: Date
  },
  data: function() {
    return {
      daysOfWeek: ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'],
      weekDay: '',
      day: 0
    }
  },
  created() {
    this.weekDay = this.daysOfWeek[this.date.getDay()];
    this.day = this.date.getDate();
  }
}
</script>

<style lang="css">
.daycard {
  width: 150px;
  height: auto;
  display: inline-block;
  color: #dd0404;

}
.daytop {
  border-bottom: 1px dotted;
}
.daytext {
  font-size: 25px;
  position: relative;
  display: inline-block;
}
.weekday {
  float: none;
  display: inline-block;
  font-size: 15px;
  position: relative;
}
.user {
  font-size: 11px;
}
.today {
  border-radius: 5px;
  border-color: #dd0404;
}
.userlist {
  list-style-type: none;
  padding-left: 0px;
}
.listentry {
  border-bottom: 1px solid;
  color: #444;
}
@media screen and (max-width: 360px) {
  .daycard {
    width: 35px;
    font-size: 10px;
  }
}
@media screen and (min-width: 600px) {
  .daycard {
    width: 45px;
    font-size: 15px;
  }
}
@media screen and (min-width: 1200px) {
  .daycard {
    width: 150px;
    font-size: 15px;
  }
}
</style>
